.main-nav {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}
.main-nav__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 2px;
  padding-bottom: 24px;
  border-top: 1px solid #485461;
  border-bottom: 1px solid #000000;
}
.main-nav__logo-image {
  display: block;
  padding: 0;
  margin-left: -71px;
}
.main-nav__logo-image .star,
.main-nav__logo-image .glasses {
  display: none;
}
.main-nav__logo-image:hover path {
  fill: #d22856;
}
.main-nav__logo-image:active path {
  fill: rgba(255, 255, 255, 0.3);
}
.main-nav__toggle {
  margin-top: 18px;
}
.main-nav__list {
  width: 320px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}
@media (max-width: 959px) {
  .main-nav__toggle {
    position: relative;
    top: 0;
    right: 0;
    display: block;
    width: 50px;
    height: 24px;
    border: none;
    font: inherit;
    color: inherit;
    cursor: pointer;
  }
  .main-nav--closed .main-nav__toggle {
    background: url("../img/icon-menu-burger.svg") no-repeat 50% 50%;
  }
  .main-nav--closed .main-nav__toggle:hover {
    background: url("../img/icon-menu-burger-red.svg") no-repeat 50% 50%;
  }
  .main-nav--closed .main-nav__toggle:active {
    background: url("../img/icon-menu-burger-red.svg") no-repeat 50% 50%;
    opacity: 0.3;
  }
  .main-nav--opened .main-nav__toggle {
    width: 24px;
    height: 24px;
    background: url("../img/icon-menu-cross.svg") no-repeat 50% 50%;
  }
  .main-nav--opened .main-nav__toggle:hover {
    background: url("../img/icon-menu-cross-red.svg") no-repeat 50% 50%;
  }
  .main-nav--opened .main-nav__toggle:active {
    background: url("../img/icon-menu-cross-red.svg") no-repeat 50% 50%;
    opacity: 0.3;
  }
  .main-nav--opened {
    background-color: #283645;
  }
  .main-nav--closed {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .main-nav--closed .main-nav__wrapper {
    border: none;
  }
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
  .main-nav--nojs {
    background-color: #283645;
  }
  .main-nav--nojs .main-nav__wrapper {
    border-top: 1px solid #485461;
    border-bottom: 1px solid #000000;
  }
}
